@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-inline {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    max-width: 1980px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

.container {
  @include content-padding-inline;
}

@mixin hover($property, $toValue) {
  transition: #{$property} $effect-duration ease-in-out;
  &:hover {
    z-index: 1;
    #{$property}: $toValue;
    @content;
  }
}

@mixin page-grid {
  display: grid;

  --columns: 2;
  
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;
    
    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}
